#mh_body{
    width:960px;
    background-color:#F0F8FF;
    margin: auto;
}
#mh_search{
    width:100%;
}
.search_ul li{
    list-style-type: none;
    display:block;
    float:left;
    padding:2px 5px;
    height:25px;
    line-height: 25px;
    text-align: center;
    margin:5px 10px;
    border-radius: 3px;
}
.search_ul{
    display: block;
    width:100%;
    overflow: auto;
    padding:0px;
    margin:0px;
}
.select{
    background-color:#87CEFA;
}
#show_body{
    width:100%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    overflow: auto;
}
.show_every{
    width:152px;
    height:260px;
    margin:20px;
    float:left;
    -webkit-box-shadow: 2px 2px 5px #a1a3a6,-1px -1px 3px  #a1a3a6;
            box-shadow: 2px 2px 5px #a1a3a6,-1px -1px 3px  #a1a3a6;
    
}
.man_img{
    width:100%;
    height:203px;
}
.show_img{
    width:100%;
    height:203px;
}
.show_every a{
    text-decoration: none;
    
}
.man_name{
    display:block;
    width:100%;
    margin:0px;
    color:#6f599c;
    height:25px;
    line-height: 25px;
    text-align: center;
    white-space: nowrap;
    -o-text-overflow:ellipsis;
       text-overflow:ellipsis;
    overflow:hidden;
}
.author{
    font-size:15px;
    color:#33a3dc;
}
.author_name{
    display:block;
    width:100%;
    margin:0px;
    height:25px;
    line-height: 25px;
    text-align: center;
    white-space: nowrap;
    -o-text-overflow:ellipsis;
       text-overflow:ellipsis;
    font-size:15px;
    color:#33a3dc;
    overflow:hidden;
}
.page{
    width:100%;
    height:30px;
    padding:20px 0px 50px 20px;
    
}
.page_back{
    display:block;
    width:80px;
    height:30px;
    text-align: center;
    line-height: 30px;
    float:left;
    margin:0px 10px;
    border:1px solid #009ad6;
}
.page_go{
    display:block;
    width:80px;
    height:30px;
    text-align: center;
    line-height: 30px;
    float:left;
    margin:0px 10px;
    border:1px solid #009ad6;
}
.page_now{
    display:block;
    height:30px;
    text-align: center;
    line-height: 30px;
    float:left;
    margin:0px 20px;
}
.page_all{
    display:block;
    height:30px;
    text-align: center;
    line-height: 30px;
    float:left;
    margin:0px 20px;
}


